import Layout from "../components/Layout";
import Link from "next/link";

const ProfileLink = props => (
    <div>
        <Link href ={`/p/[profile]`} as={`/p/${props.profile}`}>
            <a> go to {props.profile}'s profiles </a>
         </Link>
    </div>
);

const Index = () => (
  <div>
    <Layout>
      <p>index page</p>
      <ProfileLink profile="juhee" />
      <ProfileLink profile="minju" />
      <ProfileLink profile="jawon" />
    </Layout>
  </div>
);

export default Index;
